<template>
  <div class="container">
    <div class="bg">
      <img src="../images/yarhushta.png" alt="">
      <div class="overlay">
        <h1 class="title">Armenian Youth</h1>
        <social-links />
      </div>
    </div>
  </div>
</template>

<script>
import SocialLinks from './SocialLinks'
export default {
  name: 'BusinessCard',
  components: {
    SocialLinks
  }
}
</script>

<style lang="scss" scoped>
  $bg: rgba(0, 0, 0, 0.2);
  .link-list {
    position: absolute;
    bottom: 0;
    display: flex;
    justify-content: space-between;
    height: 20%;
    border-top: 1px solid white;
    width: 100%;
  }
  .link {
    display: inline-block;
  }

  *,
  *:before,
  *:after {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
  }

  .container {
    position: relative;
    height: 100%;
    margin-left: auto;
    margin-right: auto;
    width: 420px;
  }
  .bg,
  .overlay {
    text-align: center;
  }

  img,
  .overlay {
    transition: .3s all;
    border-radius: 3px;
  }

  .bg {
    float: left;
    position: relative;
    margin: .5%;
    img {
      width: 100%;
      margin-bottom: -4px;
    }
    .overlay {
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      width: 100%;
      background: $bg;
      color: #fff;
      opacity: 0;
    }
    &:hover {
      .overlay {
        opacity: 1;
      }
      img {
        -webkit-filter: blur(2px);
        filter: blur(2px);
      }
    }
  }
</style>



